  font-family: sans-serif;
}

dialog {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-items: space-between;
}

div[class\$='card'] {
  border-radius: 5px;
  border: 1px solid gray;
  box-shadow: 1px 1px 10px darkgray;
  padding: 1rem;
}

div[class\$='card']:hover {
  box-shadow: 1px 1px 15px black;
}

.cards {
  display: grid;
  padding: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.buttons-section {
  display: flex;
  gap: 1rem;
  justify-content: end;
}

button {
  appearance: none;
  padding: .5rem 2rem;
  border: none;
  border-radius: 5px;
  min-width: 6rem;
}

button:hover {
  color: whitesmoke;
  background-color: black;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,4BAA4B;AAC9B;;AAEA;EACE,kBAAkB;EAClB,sBAAsB;EACtB,iCAAiC;EACjC,aAAa;AACf;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,aAAa;EACb,2DAA2D;EAC3D,SAAS;AACX;;AAEA;EACE,aAAa;EACb,SAAS;EACT,oBAAoB;AACtB;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,YAAY;EACZ,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,iBAAiB;EACjB,uBAAuB;AACzB","sourcesContent":["* {\n  font-family: sans-serif;\n}\n\ndialog {\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n  justify-items: space-between;\n}\n\ndiv[class$='card'] {\n  border-radius: 5px;\n  border: 1px solid gray;\n  box-shadow: 1px 1px 10px darkgray;\n  padding: 1rem;\n}\n\ndiv[class$='card']:hover {\n  box-shadow: 1px 1px 15px black;\n}\n\n.cards {\n  display: grid;\n  padding: 1rem;\n  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n  gap: 1rem;\n}\n\n.buttons-section {\n  display: flex;\n  gap: 1rem;\n  justify-content: end;\n}\n\nbutton {\n  appearance: none;\n  padding: .5rem 2rem;\n  border: none;\n  border-radius: 5px;\n  min-width: 6rem;\n}\n\nbutton:hover {\n  color: whitesmoke;\n  background-color: black;\n}"],"sourceRoot":""}]);
